import React, { Component } from 'react';
import mixitup from 'mixitup';
import portfolioData from "../portfolio_data.json";


export class Portfolio extends Component {
  displayName = Portfolio.name;

  componentDidMount() {
    // Mixitup Settings
    var containerEl = document.querySelector('#portfolioList');
    var mixer = mixitup(containerEl, {
      selectors: {
        control: '[data-mixitup-control]'
      }
    });
  }

  render() {

    return (
      <section id="portfolio" className="page-section">
        <div className="container text-center wow fadeIn">
          <h2>Portfolio</h2>
          <hr className="colored" />
          <p>Here are some few projects that I've worked on.</p>
          <div className="controls mt-3">
            <button type="button" className="control btn btn-secondary btn-sm mx-2 mb-4" data-mixitup-control data-filter="all">All</button>
            <button type="button" className="control btn btn-secondary btn-sm mx-2 mb-4" data-mixitup-control data-filter=".UNI-PROJECT">UNI PROJECT</button>
            <button type="button" className="control btn btn-secondary btn-sm mx-2 mb-4" data-mixitup-control data-filter=".THESIS">THESIS</button>
            <button type="button" className="control btn btn-secondary btn-sm mx-2 mb-4" data-mixitup-control data-filter=".HOBBY">HOBBY</button>
            <button type="button" className="control btn btn-secondary btn-sm mx-2 mb-4" data-mixitup-control data-filter=".WORK">WORK</button>
          </div>
          <div className="portfolio-grid clearfix" id="portfolioList">

            {Object.keys(portfolioData).map(index => {

              return (<div className={"mix " + portfolioData[index].category} key={index}>
                    <div className="portfolio-wrapper">
                      <img src={portfolioData[index].img} alt="" />
                        <div className="caption">
                        <div className="caption-text">

                        {((index) => {
                            if (portfolioData[index].url) {
                              return < a className="text-title" href={portfolioData[index].url} target="_blank" rel="noopener noreferrer" > {portfolioData[index].title}</a >;
                            } else {
                              return <a className="text-title">{portfolioData[index].title}</a>;
                            }
                          })(index)}
                      
                           
                            <span className="text-category">{portfolioData[index].short_description}</span>
                            </div>
                            <div className="caption-bg"></div>
                        </div>
                    </div>
                </div>);
            })}            
            
          </div>
        </div>
    </section>

    );
  }

}



