import React from 'react';
import { connect } from 'react-redux';

const Timeline = props => (
  <section id="timeline">
    <div classNameName="container">
      <div className="row">
        <div className="col-md-12">
          <div className="main-timeline">
            <a className="timeline">
              <div className="timeline-icon"><i className="fa fa-archway"></i></div>
              <div className="timeline-content">
                <h3 className="title">Bachelor in Innovative Programming. 2013-2016</h3>
                <p className="description">                  
                  I stared at Linköping University, Sweden, to studie my newly found interesst in programming. It is a very hands on program, with focus on the craft and to studie real examples. This involved alot of projects with hightech IT companies.
                </p>
              </div>
            </a>
            <a className="timeline">
              <div className="timeline-icon"><i className="fa fa-rocket"></i></div>
              <div className="timeline-content">
                <h3 className="title">Active in student life: 2014-2016</h3>
                <p className="description">I was in charge of all bars and beverage in D-Group (Party committee) 2014-2015. 2016-2017 I was study board chariman and class manager.
                </p>
              </div>
            </a>
            <a className="timeline">
              <div className="timeline-icon"><i className="fa fa-book"></i></div>
              <div className="timeline-content">
                <h3 className="title">Master: Computer Science: 2016-2019</h3>
                <p className="description"> With a strong curiosity for AI I decided to focus on AI and Machine Learning. More specifcly I've read courses in data mining, AI, machine learning, text mining and projects in the subject. </p>
              </div>
            </a>
            <a className="timeline">
              <div className="timeline-icon"><i className="fa fa-globe-asia"></i></div>
              <div className="timeline-content">
                <h3 className="title">Adventures: Spring of 2018</h3>
                <p className="description">
                  By far my longest adventure. Trekking the whole 1300km long trail of the south island of New Zeeland. Alot of unexpected things happend along the months traveling. One of them was to watch the sunrise 5416 meters up in the Himalayas.
                                </p>
              </div>
            </a>
          </div>
        </div>
      </div>
    </div>
  </section>
);

export default Timeline;



