import React, { Component } from 'react';

export class Photos extends Component {
  displayName = Photos.name;

  render() {
    return (
      <section className="p-0" id="portfolio">
        <div className="container-fluid p-0">
          <div className="row no-gutters popup-gallery">
            <div className="col-lg-4 col-sm-6">
              <a className="portfolio-box" href="/img/1.jpg">
                <img className="img-fluid" src="/img/thumb/1.jpg" alt="" />
                <div className="portfolio-box-caption">
                  <div className="portfolio-box-caption-content">
                    <div className="project-category text-faded">
                      New Zeeland
                  </div>
                    <div className="project-name">
                      Somewhere in the wild
                  </div>
                  </div>
                </div>
              </a>
            </div>
            <div className="col-lg-4 col-sm-6">
              <a className="portfolio-box" href="/img/jag_jungel.jpg">
                <img className="img-fluid" src="/img/thumb/jag_jungel.jpg" alt="" />
                <div className="portfolio-box-caption">
                  <div className="portfolio-box-caption-content">
                    <div className="project-category text-faded">
                      Tanzania
                  </div>
                    <div className="project-name">
                      Bellow Mt. Kilimanjaro
                  </div>
                  </div>
                </div>
              </a>
            </div>
            <div className="col-lg-4 col-sm-6">
              <a className="portfolio-box" href="/img/grabbarna_solnedgång.jpg">
                <img className="img-fluid" src="img/thumb/grabbarna_solnedgång_mini.jpg" alt="" />
                <div className="portfolio-box-caption">
                  <div className="portfolio-box-caption-content">
                    <div className="project-category text-faded">
                      Tanzania
                  </div>
                    <div className="project-name">
                      On our way up!
                  </div>
                  </div>
                </div>
              </a>
            </div>
            <div className="col-lg-4 col-sm-6">
              <a className="portfolio-box" href="/img/taranaki.jpg">
                <img className="img-fluid" src="img/thumb/taranaki.jpg" alt="" />
                <div className="portfolio-box-caption">
                  <div className="portfolio-box-caption-content">
                    <div className="project-category text-faded">
                      New Zeeland
                  </div>
                    <div className="project-name">
                      Mt. Taranaki
                  </div>
                  </div>
                </div>
              </a>
            </div>
            <div className="col-lg-4 col-sm-6">
              <a className="portfolio-box" href="/img/nepal.jpg">
                <img className="img-fluid" src="/img/thumb/nepal.jpg" alt="" />
                <div className="portfolio-box-caption">
                  <div className="portfolio-box-caption-content">
                    <div className="project-category text-faded">
                      Nepal
                  </div>
                    <div className="project-name">
                      Annapurna
                  </div>
                  </div>
                </div>
              </a>
            </div>
            <div className="col-lg-4 col-sm-6">
              <a className="portfolio-box" href="/img/mt_doom.jpg">
                <img className="img-fluid" src="/img/thumb/mt_doom.jpg" alt="" />
                <div className="portfolio-box-caption">
                  <div className="portfolio-box-caption-content">
                    <div className="project-category text-faded">
                      New Zeeland
                  </div>
                    <div className="project-name">
                      Mt. Ngauruhoe (aka. Mt. Doom)
                  </div>
                  </div>
                </div>
              </a>
            </div>
          </div>
        </div>
      </section>

    );
  }
}


