import React from 'react';
import { connect } from 'react-redux';

const Contact = props => (
  <section id="contact">
    <div className="container">
      <div className="row">
        <div className="col-lg-8 mx-auto text-center">
          <h2 className="section-heading">Let's Get In Touch!</h2>
          <hr className="my-4"/>
            <p className="mb-5">If you want to offer a job, want to collaborate over a project or just want to have a chat then throw me an email!</p>
          </div>
        </div>
        <div className="row">          
          <div className="col-lg-12 mr-auto text-center">
            <i className="fa fa-envelope fa-3x mb-3"/>
            <p>
              <a id="mail" href="mailto:victor.dannetun@live.com">victor.dannetun@live.com</a>
            </p>
          </div>
        </div>
      <div className="row">        
        <div className="col-lg-12 mr-auto text-center">
          <hr className="my-4" />
          <a href="https://www.linkedin.com/in/victor-dannetun-a65722131"><i className="fab fa-linkedin fa-3x mb-3" /></a>
          </div>
        </div>

      </div>
    </section>
);

export default Contact;



