import React from 'react';

const About = props => (
  <section className="bg-primary" id="about">
    <div className="container">
      <div className="row">
        <div className="col-lg-8 mx-auto text-center">
          <h2 className="section-heading">Who am I? </h2>
          <hr className="my-4"/>
          <p className="mb-4 text-left">My name is Victor Dannetun and I’m soon leaving the student life with a masters degree in Data Science in Artificial Intelligence and Machine Learning. <br/>
            All my saved money the past years have been spent on travels to far away mountains. There is something exciting about mountains, the environment, the goal, and the feeling of accomplishment afterward. The mountains trains the patience, something I found very useful in life and when searching for that missing semicolon.<br/>
   At 2012, I brought home a programming book, curious of knowing how the game server I had setup with help from a dummies guide in my room actually worked. My interest for computers had grown and 2013 I moved from Stockholm to Linköping to studie programming. I learnt to make video games, websites and operating systems. I got experience of working in scrum teams in school projects and doing real projects for companies on my spare time. In Januari 2016 I started my own business which I still run alongside my studies.
   When I was about done with my bachelor I found AI to be very interesting. Now after some courses in the subject I’m even more compelled to learn more.</p>
          <a className="btn btn-primary btn-xl js-scroll-trigger" target="_blank" href="victor-dannetun-cv.pdf">Resume (Swedish)</a>
        </div>
      </div>
    </div>
  </section>
);

export default About;



